input:focus, textarea:focus {
  outline: none; }

.SearchField.-home .searchfield {
  width: 600px;
  height: 70px;
  font-size: 30px;
  border-radius: 5px;
  padding: 10px 70px 10px 20px;
  position: relative;
  background-color: white;
  -webkit-box-shadow: 4px 15px 20px 5px rgba(34, 34, 34, 0.4), inset 0 0 3px 0 rgba(34, 34, 34, 0.4);
