  @media only screen and (max-width: 640px) {
    .SearchField.-home .searchfield {
      width: 100%;
      margin: 0; } }

.SearchField.-home .button {
  position: absolute;
  height: 40px;
  width: 40px;
  padding: 0;
  right: 20px;
  top: 15px; }

.SearchField .searchfield {
