.SearchField .button {
  padding: 18px;
  width: 25px;
  position: fixed;
  z-index: 102;
  right: 10px;
  background-repeat: no-repeat;
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  background-color: transparent;
