/* Fonts */
/* Colors */
input:focus, textarea:focus {
  outline: none; }

.Home .welcome, .Home .introduction {
  text-align: center;
  max-width: 700px;
  margin: 50px auto 20px auto; }
  @media only screen and (max-width: 900px) {
    .Home .welcome, .Home .introduction {
      margin: 20px auto 20px auto; } }

.Home .picturegallery {
  max-width: 1200px;
  margin: 0 auto;
  display: grid;
  grid-gap: 20px 20px;
  grid-template-columns: repeat(auto-fill, minmax(200px, 1fr)); }
  @media only screen and (max-width: 900px) {
    .Home .picturegallery {
      grid-gap: 20px 8px;
      grid-template-columns: repeat(auto-fill, minmax(140px, 1fr)); } }
  .Home .picturegallery .picture {
    width: 200px;
    height: auto;
    justify-self: center; }
    @media only screen and (max-width: 900px) {
      .Home .picturegallery .picture {
        width: 135px; } }

.Home .search {
  position: fixed;
  z-index: 100;
  margin-top: 10vh;
  text-align: center;
  left: calc(50% - 300px); }
  @media only screen and (max-width: 640px) {
    .Home .search {
      margin: 2vh 20px 0 20px;
      width: calc(100% - 40px);
      left: 0; } }
  .Home .search .btn {
    cursor: pointer;
    border-radius: 2px;
    font-weight: 600;
    display: inline-block;
    color: white;
    margin-top: 30px;
    padding: 5px 14px;
    -webkit-box-shadow: 3px 9px 15px 2px rgba(34, 34, 34, 0.4);
            box-shadow: 3px 9px 15px 2px rgba(34, 34, 34, 0.4);
    background-color: #EB3232; }
  .Home .search .btn:hover {
    color: #F6F6F6;
    background-color: #910505;
    text-decoration: none; }
  .Home .search .poweredbyquote {
    font-size: 12px;
    margin-top: 5px;
    position: absolute;
    right: 5px; }

.Home .langs {
  text-align: center;
  margin: 0 0 50px 0;
  padding: 0;
  font-size: 16px;
  font-weight: 600; }
  .Home .langs > li {
    display: inline-block;
    list-style: none; }
  @media only screen and (max-width: 900px) {
    .Home .langs {
      margin: 0 0 30px 0; } }
  .Home .langs a {
    color: #E9E9E9;
    padding: 0 8px;
    margin: 0;
    text-decoration: none; }
    .Home .langs a.active, .Home .langs a:hover {
      color: #747474;
      background-color: #E9E9E9; }



