/* Fonts */
/* Colors */
input:focus, textarea:focus {
  outline: none; }

.SortDropdown {
  float: right;
  position: relative;
  margin-bottom: 15px; }
  @media only screen and (min-width: 901px) {
    .SortDropdown {
      margin-right: 380px; } }
  @media only screen and (max-width: 900px) {
    .SortDropdown {
      float: none; } }
  .SortDropdown .dropdown {
    width: 12px;
    position: inherit;
    margin-left: -20px;
    pointer-events: none; }
    @media only screen and (max-width: 900px) {
      .SortDropdown .dropdown {
        position: relative;
        margin: 0;
        right: 20px;
        top: -1px; } }
  .SortDropdown .intro {
    display: inline-block;
    margin: 0 7px 0 0; }
  .SortDropdown > select {
    appearance: none;
    -webkit-appearance: none;
    -moz-appearance: none;
    border: 0;
    border-radius: 2px;
    background-color: #F6F6F6;
    font-size: 16px;
    padding: 0 20px 0 5px;
    margin: 0;
    font-family: "Open Sans", Helvetica, sans-serif;
    font-weight: 600;
    cursor: pointer;
    outline: none;
    border: none;
    border-radius: 0;
    font-family: "Open Sans", Helvetica, sans-serif;
    font-size: 16px; }
    @media only screen and (max-width: 900px) {
      .SortDropdown > select {
        margin: 10px 0 0 0;
        max-width: 260px; } }



